<template>
  <div>
    <div v-if="loading">Loading...</div>
    <div v-else>
      <div class="field is-pulled-right">
        <div class="control">
          <div class="select">
            <select class="select" v-model="matchDay" @change="fetchLeagueTable">
              <option value="">Last match day</option>
              <option v-for="day in matchDays" :key="day" v-text="day" :value="day"></option>
            </select>
          </div>
        </div>
      </div>
      <table class="table is-hoverable is-fullwidth">
        <thead>
          <tr>
            <th><abbr title="Position">Pos</abbr></th>
            <th class="is-hidden-mobile"><abbr title="Crest">Crest</abbr></th>
            <th>Team</th>
            <th><abbr title="Played">Pld</abbr></th>
            <th class="is-hidden-mobile"><abbr title="Won">W</abbr></th>
            <th class="is-hidden-mobile"><abbr title="Drawn">D</abbr></th>
            <th class="is-hidden-mobile"><abbr title="Lost">L</abbr></th>
            <th class="is-hidden-mobile"><abbr title="Goals for">GF</abbr></th>
            <th class="is-hidden-mobile"><abbr title="Goals against">GA</abbr></th>
            <th class="is-hidden-mobile"><abbr title="Goal difference">GD</abbr></th>
            <th><abbr title="Points">Pts</abbr></th>
          </tr>
        </thead>
        <tbody>
          <table-row
            v-for="standing in leagueTable.standing"
            :key="standing.position"
            :standing="standing"
          >
          </table-row>
        </tbody>
      </table>
    </div>
  </div>
</template>

<script>
import { mapGetters } from 'vuex';
import { LEAGUE_TABLE, LEAGUE_TEAMS } from '@/store/modules/competitions/loading-types';
import TableRow from '@/components/LeagueTableRow';

export default {
  name: 'LeagueTable',
  props: {
    competition: {
      required: true,
      validator: (id) => id > 0,
    },
  },
  components: {
    TableRow,
  },
  beforeRouteEnter(to, from, next) {
    next(vm => vm.fetchLeagueTable());
  },
  beforeRouteUpdate(to, from, next) {
    this.fetchLeagueTable();
    next();
  },
  watch: {
    $route: 'fetchLeagueTable',
  },
  data() {
    return {
      matchDay: '',
      matchDays: 48,
    };
  },
  computed: {
    loading() {
      return this.$store.getters.isLoading([LEAGUE_TABLE, LEAGUE_TEAMS]);
    },
    ...mapGetters({
      leagueTable: 'getLeagueTable',
    }),
  },
  methods: {
    fetchLeagueTable() {
      this.$store.dispatch('fetchLeague', { id: this.competition, params: { matchday: this.matchDay } });
      this.$store.dispatch('fetchTeams', { id: this.competition });
    },
  },
};
</script>



